import gql from "graphql-tag";
import { withApollo } from 'react-apollo';

const GET_POSTS = gql`
    query allPosts {
      allPosts {
        id
        title
        votes
        url
        createdAt
      }
      _allPostsMeta {
        count
      }
    }
  `
const skip = 0;
const variables = {
    first: 10,
    skip: 0
}

const App = ({client}) => {
  const someQueryResult = client.query({ query: GET_POSTS, skip, variables});
  someQueryResult.then(result => console.log(result));
  debugger;
  return someQueryResult.then(result => console.log(result));
}

export default withApollo(App);